.toggle{display:inline-flex;align-items:center;cursor:pointer}.toggle__box{position:relative;width:40px;height:20px;margin-right:10px;flex-shrink:0}.toggle__input{width:100%;height:100%;opacity:0;position:absolute;cursor:pointer}.toggle__input:checked+.toggle__checker{border-color:#bc9cff}.toggle__input:checked+.toggle__checker:before{background-image:linear-gradient(#bc9cff 0%, #8ba4f9 100%);transform:translateX(20px)}.toggle__checker{width:100%;height:100%;border:1px solid rgba(31,32,65,0.25);border-radius:12px;background-color:white}.toggle__checker:before{position:absolute;content:'';height:12px;width:12px;left:10%;top:20%;background:rgba(31,32,65,0.25);transition:.2s;border-radius:50%}
